<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="repair-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="维修详情" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <van-cell-group>
      <van-cell title="选择维修结果" is-link clickable @click="handleSelectResult">
        <span>{{result && result.name}}</span>
      </van-cell>
      <van-cell title="故障设备" is-link clickable @click="handleSelectDevice">
        <span>{{deviceCategory && deviceCategory.name}} / {{device && device.name}}</span>
      </van-cell>
      <van-cell title="故障分类" is-link clickable @click="handleSelectReason">
        <span>{{reason && reason.name}}</span>
      </van-cell>
      <!-- <van-cell title="故障类型" is-link clickable @click="handleSelectType">
        <span>{{type && type.name}}</span>
      </van-cell> -->
      <van-cell title="故障等级" is-link clickable @click="handleSelectLevel">
        <span>{{level && level.name}}</span>
      </van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell title="维修内容(必填)："></van-cell>
      <van-field v-model="repairContent" placeholder="请填写维修内容" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell title="备注："></van-cell>
      <van-field v-model="repairRemark" placeholder="请填写备注（选填）" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell title="图像："></van-cell>
      <div class="images-container">
        <div class="image-item-show" v-for="(item, index) in imageList" :key="index" @click="handlePlayImages(index)">
          <!-- <img :src="item.url" @click="handlePlayImages(index)"> -->
          <auto-image :src="item.url" :width="108" :height="108" style="margin-bottom: 12px;margin-left: 12px;"></auto-image>
          <div class="image-remove" @click.stop="handleImageRemove(index)"></div>
        </div>
        <div class="image-item" style="background: #fff;"  @click.stop="handlePhoto1">
          <img src="../../assets/images/image-add.png">
        </div>
      </div>
    </van-cell-group>

    <van-cell-group>
      <van-cell title="视频："></van-cell>
      <div class="images-container">
        <div class="image-item-show"  v-for="(item, index) in videoList" :key="index" @click="handlePlayVideo(item)">
          <!-- <auto-image :src="item.url" :width="108" :height="108" style="margin-bottom: 12px;margin-left: 12px;"></auto-image> -->
          <img src="../../assets/images/video-default.png" class="img-video" />
          <div class="image-remove" @click.stop="handleVideoRemove(index)"></div>
        </div>
        <div class="image-item" style="background: #fff;"  @click.stop="handlePhoto2">
          <img src="../../assets/images/image-add.png">
        </div>
      </div>
    </van-cell-group>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action">
        <van-button @click="handleSave" class="action-item-main btn-save" color="rgba(16, 142, 233, 0.15)">保存</van-button>
        <van-button @click="handleSubmit" class="action-item-main" color="#108EE9">提交</van-button>
      </div>

      <van-popup 
        v-model="reasonShow"
        position="bottom"  
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">
          选择故障分类
        </div>
        <a-scroll ref="filterScroller" v-if="reasonShow" class="filter-scroll">
          <van-radio-group v-model="reasonRadio.id">
            <van-cell-group>
              <van-cell :title="item.name" :label="item.description" clickable @click="reasonRadio = {...item}" v-for="(item,index) in reasonOptions" :key="index">
                <van-radio slot="right-icon" :name="item.id"  />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onReasonSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup 
        v-model="typeShow"
        position="bottom"  
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">
          选择故障类型
        </div>
        <a-scroll ref="filterScroller" v-if="typeShow" class="filter-scroll">
          <van-radio-group v-model="typeRadio.id">
            <van-cell-group>
              <van-cell :title="item.name" :label="item.description" clickable @click="typeRadio = {...item}" v-for="(item,index) in typeOptions" :key="index">
                <van-radio slot="right-icon" :name="item.id"  />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onTypeSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup 
        v-model="levelShow"
        position="bottom"  
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">
          选择故障等级
        </div>
        <a-scroll ref="filterScroller" v-if="levelShow" class="filter-scroll">
          <van-radio-group v-model="levelRadio.id">
            <van-cell-group>
              <van-cell :title="item.name" :label="item.description" clickable @click="levelRadio = {...item}" v-for="(item,index) in levelOptions" :key="index">
                <van-radio slot="right-icon" :name="item.id"  />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onLevelSelect">确定</van-button>
        </div>
      </van-popup>

      <van-action-sheet 
        v-model="resultShow"
        :actions="resultOptoins"
        close-on-click-action 
        @select="onResultSelect" />
    
    
    
      <van-dialog
        v-model="editShow"
        show-cancel-button
        :before-close="beforeClose"
      >
        <van-cell title="描述"></van-cell>
        <van-field
          ref="desInput"
          v-model="description"
          type="textarea"
          :clearable="true"
        />
      </van-dialog>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import AScroll from '../../components/Scroll'
import navigate from '../../utils/navigate.js'
import AutoImage from '../../components/AutoImage'
import db from '../../db'
import io from '../../api'
import moment from 'moment'
import {uploadImage, uploadVideo} from '../../utils/upload.js'

export default {
  components: {
    Page,
    AScroll,
    AutoImage
  },
  data: () => ({
    headerHeightVW: 0,
    levelOptions: JSON.parse(localStorage.getItem('level_options')),
    reasonOptions: JSON.parse(localStorage.getItem('reason_options')),
    typeOptions: JSON.parse(localStorage.getItem('type_options')),
    resultOptoins: [
      {id: 1, name: '临时维修'},
      {id: 2, name: '完全修复'}
    ],
    orderid: null,
    level: null,
    device: null,
    deviceCategory: null,
    type: null,
    reason: null,
    result: {id: 1, name: '临时维修'},
    resultShow: false,
    reasonShow: false,
    reasonRadio: null,
    typeShow: false,
    typeRadio: null,
    levelShow: false,
    levelRadio: null,
    imageList: [],
    videoList: [],
    UIPhotoViewer: null,
    editShow: false,
    description: '',
    imgFile: '',
    repairContent: '',
    repairRemark: '',
    orderDetail: null,
    imageFilter: null,
    videoCompression: null,
  }),
  methods: {
    onReady (header, main) {
      this.header = header

      this.headerHeightVW = (this.header.h + 1) * 100 / this.header.w

      this.orderid = navigate.getParam('orderid')

      this.loadOrderDetail()
    },

    onShow () {
      if (localStorage.getItem('device_select')) {
        this.device = JSON.parse(localStorage.getItem('device_select'))

        localStorage.setItem('device_select', '')
      }

      if (localStorage.getItem('device_type_select')) {
        this.deviceCategory = JSON.parse(localStorage.getItem('device_type_select'))

        localStorage.setItem('device_type_select', '')
      }
    },

    loadOrderDetail () {
      db.getOrderDetail(this.orderid).then(data => {
        this.level = data.level
        this.device = data.device
        this.deviceCategory = data.deviceCategory
        this.type = data.faultType
        this.reason = data.reason
        this.result = data.repairResult == 1 ? {id: 1, name: '临时维修'} : {id: 2, name: '完全修复'}
        this.repairContent = data.repairContent
        this.repairRemark = data.repairRemark
        this.imageList = data.repairImages || []
        this.videoList = data.repairVideos || []

        this.orderDetail = data

        localStorage.setItem('device_select', JSON.stringify(this.device))

      })
    },
    

    onClickLeft() {
      navigate.back()
    },

    handleSelectResult () {
      this.resultShow = true
    },

    onResultSelect (item) {
      this.result = item
    },

    handleSelectReason () {
      this.reasonShow = true
      this.reasonRadio = {...this.reason}
    },

    onReasonSelect () {
      this.reason = {...this.reasonRadio}
      this.reasonShow = false
    },

    handleSelectType () {
      this.typeShow = true
      this.typeRadio = {...this.type}
    },

    onTypeSelect () {
      this.type = {...this.typeRadio}
      this.typeShow = false
    },

    handleSelectLevel () {
      this.levelShow = true
      this.levelRadio = {...this.level}
    },

    onLevelSelect () {
      this.level = {...this.levelRadio}
      this.levelShow = false
    },

    handlePhoto1 () {
      api.actionSheet({
        buttons: ['打开照相机', '从相册获取']
      }, (ret, err) => {
        var index = ret.buttonIndex;
        let sourceType = ''
        if (index === 1) {
          sourceType = 'camera'
        } else if (index === 2) {
          sourceType = 'album'
        }

        sourceType && api.getPicture({
          sourceType: sourceType,
          destinationType: 'url',
          preview: true,
          // quality: 50,
          mediaValue: 'pic'
        }, (ret, err) => {
          // alert(JSON.stringify(ret))
          if (ret.data) {
            this.imgFile = ret.data
            this.description = ''
            this.editShow = true
            setTimeout(() => {
              this.$refs.desInput.focus()
            }, 200)

          }
        })
      })

    },

    handlePhoto2 () {
      api.actionSheet({
        buttons: ['打开照相机', '从相册获取']
      }, (ret, err) => {
        var index = ret.buttonIndex;
        let sourceType = ''
        if (index === 1) {
          sourceType = 'camera'
        } else if (index === 2) {
          sourceType = 'album'
        }

        sourceType && api.getPicture({
          sourceType: sourceType,
          destinationType: 'url',
          preview: true,
          quality: 100,
          videoQuality: 'high',
          mediaValue: 'video',
          saveToPhotoAlbum: true
        }, (ret, err) => {

          if (ret.data) {
            // this.videoList.push({url: ret.data})

            if (api.systemType == 'ios') {
              this.$toast.loading({
                duration: 0,
                forbidClick: true,
                loadingType: 'spinner',
                message: '正在压缩...'
              })

              if (!this.videoCompression) {
                this.videoCompression = api.require('videoCompression')
              }

              this.videoCompression.compression({
                path: ret.data,
                quality: 'medium'
              },(ret1, err1) => {
                // alert(JSON.stringify(ret1))
                if (ret1.eventType == 'completed') {
                  this.videoList.push({ url: ret1.path })
                  this.$toast.clear()
                }
              })
            } else {
              this.videoList.push({ url: ret.data })
            }
          }

          // if (ret.data) {

          //   const fileExtension = ret.data.substring(ret.data.lastIndexOf('.') + 1)

          //   const filePath = 'fs://hsvideo/' + new Date().getTime() + '.' + fileExtension

          //   alert(ret.data)
          //   alert(filePath)

          //   api.download({
          //     url: ret.data,
          //     savePath: filePath,
          //     report: true,
          //     cache: true,
          //     allowResume: true
          //   }, (ret, err) => {
          //     alert(JSON.stringify(ret))
          //     if (ret.savePath && ret.state == 1) {
          //       this.videoList.push({url: ret.savePath})
          //     }
          //   })
            
          // }
        })
      })
    },

    beforeClose(action, done) {
      if (action === 'confirm') {
        this.imageList.push({url: this.imgFile, description: this.description})
      } else {
        this.imageList.push({url: this.imgFile, description: ''})
      }
      done()
    },

    handleVideoRemove (index) {
      this.videoList.splice(index, 1)
    },

    handleImageRemove (index) {
      this.imageList.splice(index, 1)
    },

    handlePlayVideo (item) {
      api.openVideo({
        url: item.url
      })
    },

    handlePlayImages (index) {
      localStorage.setItem('photos', JSON.stringify(this.imageList))
      navigate.open('photoViewer', {index: index})
    },

    handleSelectDevice () {
      navigate.open('device')
    },

    handleSave () {
      this.onSave().then(() => {
        this.$toast('保存成功')
      })
    },

    handleSubmit () {
      this.onSave().then((data) => {
        
        // 检查是否有网络链接
        if (api.connectionType == 'none') {
          alert('维修结果已保存，请在有网的时候提交！')
        } else {

          this.$toast.loading({
            duration: 0,
            forbidClick: true,
            loadingType: 'spinner',
            message: '正在提交...'
          })

          if (!data.signPhoto) {
            data.signPhoto = []
          }

          // 上传图片资源
          uploadImage(data.signPhoto.map(item => ({url: item}))).then(sp => {
            data.signPhoto = sp

            uploadImage(data.repairImages).then(s => {
              data.repairImages = s
                // 上传视频
                uploadVideo(data.repairVideos).then(v => {
                  data.repairVideos = v

                  // 同步数据
                  io.uploadOrder(data).then(() => {
                    io.submitOrder({id: data.id}).then(data => {
                      db.removeOrder(this.orderid).then(() => {
                        navigate.back()
                      })
                    }).catch(msg => {
                      this.$toast(msg)
                    })
                  }).catch(msg => {
                    this.$toast(msg)
                  })
                })
            })
          })
        }
      })
    },

    onSave () {
      return new Promise((resolve, reject) => {
        db.getOrderDetail(this.orderid).then(data => {

          let saveData = {
            ...data,
            repairResult: this.result.id,
            device: this.device,
            deviceCategory: this.deviceCategory,
            reason: this.reason,
            faultType: this.type,
            level: this.level,
            repairContent: this.repairContent,
            repairRemark: this.repairRemark,
            repairTime: moment().format('YYYY-MM-DD HH:mm:ss'),
            repairImages: this.imageList,
            repairVideos: this.videoList
          }

          db.updateOrder(saveData).then(() => {
            resolve(saveData)
          })
        })
      })
    }
  }
}
</script>

<style lang="scss">
.repair-page{
  .van-cell-group+.van-cell-group{
    margin-top: 12px;
  }

  .btn-save{
    .van-button__text{
      color: #108EE9;
    }
  }

  .filter-popup{
    width: 100%;
    height: 90%;

    .title{
      height: 50px;
      line-height: 50px;
      text-align: center;
      font-size: 16px;
      border-bottom: 1px solid #EBEDF0;
    }

    .filter-scroll{
      height: calc(100% - 110px);
      background: #F7F8FA;
    }
  }
}
</style>
